<template>
  <div class="fields" style="overflow: auto;flex: 0 0 500px;display: flex;height: 100%;border-left: 5px solid #e8e8e8;flex-direction: column;min-width: 500px;">
    <div style="flex-direction: column;flex: 1;display: flex;overflow: auto;padding: 20px;">
      <div class="field-block">
        <div class="flex-row" style="width: 100%;">
          <div style="flex: 1;">#{{ task.number }}</div>
          <div class="three-dots-class flex-column align-items-center justify-content-center">
            <b-icon icon="three-dots" scale="1.7"/>
          </div>
        </div>
      </div>

      <div class="field-block">
        <div class="flex-row" style="width: 100%;">
          <div v-if="summaryEditing" style="flex: 1;height: 30px;">
            <b-form-input autofocus v-model="task.summary" @blur="updateSummary"/>
          </div>
          <div v-else class="summary" @click="editSummary" style="height: 30px;">
            {{ task.summary }}
          </div>
        </div>
      </div>
      <div class="field-block">
        <div class="flex-row" style="width: 100%;padding: 0 10px 10px 10px;justify-content: space-between;">
          <div style="flex: 1;text-align: center;">
            <Assign :user="task.assign" :project="task.project.uuid" :task="task.uuid"/>
          </div>
          <div style="flex: 1;text-align: center;">
            <TaskStatus :status="task.task_status" :project="task.project.uuid" :issue_type="task.issue_type.uuid" :task_uuid="task.uuid"/>
          </div>
          <div style="flex: 1;text-align: center;">
            <TaskPriority :project="task.project.uuid" :priority="task.priority" :task_uuid="task.uuid"/>
          </div>
        </div>
      </div>
      <div class="field-block">
        <div class="flex-row">
          <div style="flex: 1;padding: 0 5px 0 0;">
            描述（富文本编辑）
          </div>
          <div style="padding: 0 5px 0 0;flex: 0 0 auto;cursor: pointer;color: #17C4BB;" class="shadow-none" v-b-modal.modal-issue-content>
            放大浏览
          </div>
        </div>
      </div>

      <b-modal size="xl" scrollable :title="task.summary" id="modal-issue-content" no-close-on-backdrop :dialog-class="{edit: !isDisplay}" :hide-footer="isDisplay"
               cancel-title="取消" ok-title="确定" @ok="updateDesc" @cancel="cancelEdit" @close="cancelEdit">
        <div v-if="isDisplay" style="padding: 20px;min-height: 500px;position: relative;" class="task-flex-auto ck-content">
          <div v-html="task.desc"/>
          <div style="position: absolute;right: 20px;top: 0;line-height: 1.2;padding: 5px 10px;background-color: #FFA500;color:#ffffff;cursor: pointer;" @click="editDesc">编辑</div>
        </div>
        <div v-else class="flex-column task-flex-auto" style="height: 100%;">
          <div id="taskToolBar" class="task-flex-none"></div>
          <div id="taskDescContainer" class="flex-column task-flex-auto task-edit-editor">
            <ckeditor :editor="editor" @ready="onReady" v-model="editDescModel" :config="editorConfig"/>
          </div>
        </div>
      </b-modal>

      <div style="border: 1px solid #e8e8e8;flex: 0 0 auto;margin-top: 5px;border-radius: 5px;overflow-y: auto;overflow-x: hidden;height: 180px;padding: 10px;" class="ck-content" v-html="task.desc"/>

      <slot name="workField"></slot>

      <div class="field-type-group option">
        <div class="flex-row field-row" v-for="c in task.field_values" :key="c.uuid">
          <div class="field-cell">{{c.name}}</div>
          <div :ref="'permBody' + c.uuid" class="field-cell-value header edit" :class="{active: visible && selectField === c.uuid}">
            <div @click="dropOptionValues(c.uuid)" class="flex-row align-items-center" style="flex: 1;">
              <div v-if="c.value">{{c.value}}</div>
              <div v-else-if="c.number_value">{{c.number_value}}</div>
              <div class="none" v-else>未设置</div>
            </div>
            <div style="position: absolute;" class="edit ibox" :class="{open: visible && selectField === c.uuid}">
              <div v-for="option in options" class="option-item flex-row align-items-center" :key="option.uuid" @click="updateOption(c.uuid, option.uuid)">
                <div style="color: #909090;" class="task-flex-auto">
                  {{ option.value }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="flex-row field-row">
          <div class="field-cell">截止日期</div>
          <div class="field-cell-time header edit">
            <b-form-datepicker
                size="sm"
                v-model="deadline"
                v-bind="zh"
                :date-format-options="{ 'year': 'numeric', 'month': 'numeric', 'day': 'numeric' }"
                :no-flip="true"
                :hide-header="true"
                @shown="onShown"
                @context="onContext"
                placeholder="未设置">
            </b-form-datepicker>
          </div>
        </div>
      </div>

      <div class="field-type-group-last option">
        <div class="flex-row field-row">
          <div class="field-cell">所属项目</div>
          <div class="field-cell-value header">{{ task.project.name }}</div>
        </div>
        <div class="flex-row field-row">
          <div class="field-cell">工作项类型</div>
          <div class="field-cell-value header">{{ task.issue_type.name }}</div>
        </div>
        <div class="flex-row field-row">
          <div class="field-cell">创建者</div>
          <div class="field-cell-value header">
            <User :user="task.owner"/>
          </div>
        </div>
        <div class="flex-row field-row">
          <div class="field-cell">创建时间</div>
          <div class="field-cell-value header">{{ task.create_time | formatDate }}</div>
        </div>
        <div class="flex-row field-row">
          <div class="field-cell">更新时间</div>
          <div class="field-cell-value header">{{ task.server_update_stamp / 1000 | formatDate }}</div>
        </div>
      </div>
    </div>
    <div style="flex: 0 0 auto;border-top: 1px solid #e8e8e8;padding: 10px;" class="flex-row align-items-center">
      <div class="watchers flex-row" style="flex: 1;">
        <User v-for="u in task.watchers" :user="u" :key="u.uuid" :includeName="false"/>
      </div>
      <div v-if="watched" style="flex: 0 0 auto;cursor: pointer;" class="flex-row align-items-center watch" @click="watchers_cancel">
        <div style="flex: 0 0 auto;color: inherit;">
          <b-icon icon="eye-slash"/>
        </div>
        <div style="flex: 0 0 auto;margin-left: 5px;color: inherit;">
          取消关注
        </div>
      </div>
      <div v-else style="flex: 0 0 auto;cursor: pointer;" class="flex-row align-items-center watch" @click="watchers_add">
        <div style="flex: 0 0 auto;color: inherit;">
          <b-icon icon="eyeglasses"/>
        </div>
        <div style="flex: 0 0 auto;margin-left: 5px;color: inherit;">
          关注
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import User from '../common/control/suser';
import Assign from './assign';
import TaskStatus from './status';
import TaskPriority from './priority';
import http from "../../utils/http";
import DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';
import { getUser } from '@/filters';

export default {
  data() {
    return {
      team: '',
      project: '',
      visible: false,
      optionValue: '',
      options: [],
      selectField: '',
      deadline: '',
      zh: {
        weekdayHeaderFormat: 'narrow',
        labelPrevDecade: '过去十年',
        labelPrevYear: '上一年',
        labelPrevMonth: '上个月',
        labelCurrentMonth: '当前月份',
        labelNextMonth: '下个月',
        labelNextYear: '明年',
        labelNextDecade: '下一个十年',
        labelToday: '今天',
        labelSelected: '选定日期',
        labelNoDateSelected: '未选择日期',
        labelCalendar: '日历',
        labelNav: '日历导航',
        labelHelp: '使用光标键浏览日期'
      },
      deadlineChanged: false,
      summaryEditing: false,
      editor: DecoupledEditor,
      editorConfig: {
        toolbar: {
          items: [
            'undo', 'redo', 'heading',
            'fontFamily', 'fontSize', 'fontColor', 'fontBackgroundColor', '|', 'bold', 'italic',
            'underline', 'strikethrough', '|', 'alignment', 'numberedList', 'bulletedList', 'outdent', 'indent', '|',
            'link', 'blockquote', 'imageUpload', 'insertTable'
          ]
        },
        ckfinder: {
          uploadUrl: "",
          options: {
            resourceType: 'Images'
          }
        }
      },
      isDisplay: true,
      editDescModel: '',
      watched: false
    }
  },
  props: {
    task: Object,
    issue_type: String
  },
  components: {
    User,
    Assign,
    TaskStatus,
    TaskPriority
  },
  created() {
    let self = this;
    self.deadlineChanged = false;
    self.team = self.$route.params.team;
    self.project = self.$route.params.project;
    self.editorConfig.ckfinder.uploadUrl = self.urls.upload.format(self.team);
    self.editDescModel = self.task.desc;
    self.formatDeadline();
    self.checkWatcher();
  },
  watch: {
    task(v) {
      let self = this;
      self.summaryEditing = false;
      self.editDescModel = v.desc;
      self.formatDeadline();
      self.checkWatcher();
    }
  },
  computed: {
    permBody: function () {
      let self = this;
      return "permBody" + self.selectField;
    }
  },
  methods: {
    onReady(editor) {
      document.querySelector( '#taskToolBar' ).appendChild( editor.ui.view.toolbar.element );
      document.querySelector( '#taskDescContainer' ).appendChild( editor.ui.getEditableElement() );
    },
    formatDeadline: function () {
      let self = this;
      if (self.task.deadline) {
        self.deadline = new Date(self.task.deadline);
      } else {
        self.deadline = null;
        return null;
      }
    },
    watchers_add: function () {
      let self = this;
      self.team = self.$route.params.team;
      http.post(self.urls.watchers_update.format(self.team, self.project, self.task.uuid, 'add')).then(function (response) {
        if (response.data.code === 200) {
          self.bus.$emit("alertSuccess", '更新成功');
          self.$parent.task_get(self.task.uuid);
        }
      }).catch(function (err) {
        self.bus.$emit("alertDanger", err.response.data.errcode);
      });
    },
    watchers_cancel: function () {
      let self = this;
      self.team = self.$route.params.team;
      http.post(self.urls.watchers_update.format(self.team, self.project, self.task.uuid, 'cancel')).then(function (response) {
        if (response.data.code === 200) {
          self.bus.$emit("alertSuccess", '更新成功');
          self.$parent.task_get(self.task.uuid);
        }
      }).catch(function (err) {
        self.bus.$emit("alertDanger", err.response.data.errcode);
      });
    },
    dropOptionValues: function (fieldUUID) {
      let self = this;
      self.options = [];
      self.selectField = fieldUUID;
      self.visible = true;
      document.addEventListener('click', self.hidePanel, false)

      http.get(self.urls.field_option_value_list.format(self.team, self.project, self.issue_type, fieldUUID), {}).then(function (response) {
        self.options = response.data;
      });
    },
    hide () {
      let self = this;
      self.visible = false
      document.removeEventListener('click', self.hidePanel, false)
    },
    hidePanel (e) {
      let self = this;
      if(self.$refs[self.permBody]) {
        if (!self.$refs[self.permBody][0].contains(e.target)) {
          self.hide();
        }
      } else {
        self.hide();
      }
    },
    updateOption: function (field, option) {
      let self = this;
      self.visible = false;
      let data = { task: self.task.uuid, field: field, option: option };
      http.post(self.urls.field_option_update.format(self.team, self.project, self.issue_type), data).then(function (response) {
        if (response.data.code === 200) {
          self.bus.$emit("alertSuccess", '更新成功');
          self.$parent.task_get(self.task.uuid);
        }
      }).catch(function (err) {
        self.bus.$emit("alertDanger", err.response.data.errcode);
      });
    },
    onShown: function () {
      let self = this;
      self.deadlineChanged = true;
    },
    onContext: function (ctx) {
      let self = this;
      if (self.deadlineChanged && ctx.selectedYMD) {
        self.deadlineChanged = false;
        let dt = new Date(ctx.selectedYMD);
        let data = { deadline: dt.getTime(), which_field: 'deadline' };
        http.post(self.urls.task_update.format(self.team, self.project, self.issue_type, self.task.uuid), data).then(function (response) {
          if (response.data.code === 200) {
            self.bus.$emit("alertSuccess", '更新成功');
            self.$parent.task_get(self.task.uuid);
          }
        }).catch(function (err) {
          self.bus.$emit("alertDanger", err.response.data.errcode);
        });
      }
    },
    editSummary: function () {
      let self = this;
      self.summaryEditing = true;
    },
    updateSummary: function () {
      let self = this;
      if (self.task.summary) {
        self.summaryEditing = false;
        let data = { summary: self.task.summary, which_field: 'summary' };
        http.post(self.urls.task_update.format(self.team, self.project, self.issue_type, self.task.uuid), data).then(function (response) {
          if (response.data.code === 200) {
            self.bus.$emit("alertSuccess", '更新成功');
            self.$parent.task_list(self.task.uuid);
          }
        }).catch(function (err) {
          self.bus.$emit("alertDanger", err.response.data.errcode);
        });
      }
    },
    updateDesc: function () {
      let self = this;
      self.isDisplay = true;
      if (self.task.desc) {
        let data = { desc: self.editDescModel, which_field: 'desc' };
        http.post(self.urls.task_update.format(self.team, self.project, self.issue_type, self.task.uuid), data).then(function (response) {
          if (response.data.code === 200) {
            self.bus.$emit("alertSuccess", '更新成功');
            self.$parent.task_get(self.task.uuid);
          }
        }).catch(function (err) {
          self.bus.$emit("alertDanger", err.response.data.errcode);
        });
      }
    },
    editDesc: function () {
      let self = this;
      self.isDisplay = false;
    },
    cancelEdit: function () {
      let self = this;
      self.isDisplay = true;
    },
    checkWatcher: function () {
      let self = this;
      self.watched = false;
      let uuid = getUser();
      if (self.task.watchers) {
        for (let i=0;i<self.task.watchers.length;i++) {
          if (self.task.watchers[i].uuid === uuid) {
            self.watched = true;
          }
        }
      }
    }
  }
}
</script>

<style scoped>
.summary { flex: 1;width: 0;height: 30px;line-height: 30px;flex: 1;font-size: 18px;text-shadow: #EEE 1px 1px 1px;color: #4F4F4F;letter-spacing: 1px;text-overflow: ellipsis;overflow: hidden;white-space: nowrap; }
.field-block { margin-top: 10px; }
.field-type-group { margin-top: 20px;border-bottom: 1px solid #e8e8e8;padding-bottom: 10px; }
.field-type-group-last { margin-top: 20px;padding-bottom: 0; }
.watch:hover { color: #17C4BB; }
.option .header {
  position: relative;
}
.option .header.active { border: 1px solid #17C4BB; }
.option .header .edit {
  overflow-y: auto;
  width: 100%;
  top: 30px;
  left: 0;
  max-height: 0;
  margin: 0;
  padding: 0;
  background-color: #fff;
  /*transition: max-height .3s ease-out;*/
}
.option .open.edit {
  max-height: 180px;
  /*-webkit-animation: slide-down .3s ease-in;*/
  /*transition: max-height .3s ease-in;*/
  transform-origin: 50% 0;
  box-shadow: 0 1px 6px rgba(0,0,0,0.2);
  z-index: 9999;
}
.option-item { padding: 0 10px;height: 30px; }
.option-item:hover { background-color: #eff6fd; }

.three-dots-class { color: #666666;flex: 0 0 30px;cursor: pointer; }
.three-dots-class:hover { color: #17C4BB; }

.link:hover { color: #17C4BB; }

.task-edit-editor { overflow-y: auto;height: 100%;min-height: 380px; }
</style>