<template>
  <component v-bind:is="currentTabComponent" :items="items" v-if="items && items.length> 0"></component>
</template>

<script>
import http from "../../utils/http";
import com from '../designer/com';
import issue_type from '../designer/issue_type';
import more from '../designer/more';
import project_info from '../designer/project_info';
import project_permission from '../designer/project_permission';
import project_field from '../designer/project_field';
import project_status from '../designer/project_status';
import field from '../designer/field';
import issue_type_permission from '../designer/issue_type_permission';
import issue_type_flow from '../designer/issue_type_flow';

export default {
  data() {
    return {
      currentTabComponent: 'com',
      items: []
    }
  },
  watch: {
    '$route' () {
      this.shift();
    }
  },
  mounted() {
    let self = this;
    self.team = self.$route.params.team;
    self.project = self.$route.params.project;
    self.shift();
    self.components_get();
  },
  methods: {
    components_get: function () {
      let self = this;
      http.get(self.urls.components.format(self.team, self.project)).then(function (response) {
        self.items = response.data;
      });
    },
    shift: function () {
      let self = this;
      self.currentTabComponent = self.attr;
    }
  },
  computed: {
    attr: function () {
      let self = this;
      return self.$route.params.attr || 'com';
    }
  },
  components: {
    com,
    issue_type,
    more,
    project_info,
    project_permission,
    project_field,
    project_status,
    field,
    issue_type_permission,
    issue_type_flow
  }
}
</script>